import { ICON_PATH } from './../../../const/icon-path.const';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { validatePassword, MessageService } from 'merc-lib';
import { MODAL_ICON_PATH } from '../../../const/img-path.const';
import { errorMsg } from 'projects/tc-lib/src/public-api';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  errorMsg = errorMsg;
  // 密碼顯示
  oldPwdVisible = false;
  newPwdVisible = false;
  newPwdConfirmVisible = false;

  validateForm: FormGroup = this.fb.group({
    oldpwd: ['', [Validators.required, this.qulifiedPassword]],
    newpwd: ['', [Validators.required, this.qulifiedPassword]],
    confirmNewpwd: ['', [Validators.required, validatePassword('newpwd'), this.qulifiedPassword]]
  });
  constructor(
    private fb: FormBuilder,
    private messgaeService: MessageService
  ) { }

  ngOnInit(): void {
  }

  private qulifiedPassword(control: FormControl) {
    const value = control.value;
    const regex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(\w{8,15})$/;
    if (regex.test(value)) {
      return null;
    } else {
      return { msg: '格式錯誤' };
    }
  }

  onSendClick(): void {
    const controls = this.validateForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsTouched();
        controls[i].updateValueAndValidity();
      }
    }
    if (this.validateForm.invalid) { return; }

    const date = new Date();
    const rocDate = `${date.getFullYear() - 1911}/${date.getMonth() + 1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
    this.messgaeService.successModal(`您的密碼以於${rocDate}變更成功，
    下次登入時請使用新密碼。`, { msgTitle: '變更成功', positiveBtnTitle: '確認', imgPath: MODAL_ICON_PATH.SUCCESS });

  }

  onCancel(): void {
    this.validateForm.reset();
  }
}
