import { Pipe, PipeTransform } from '@angular/core';
import { ApplyStatus } from './apply-status.enum';

@Pipe({
  name: 'statusDescription'
})
export class StatusDescriptionPipe implements PipeTransform {

  transform(value: number, ...args: unknown[]): unknown {
    switch (value) {
      case ApplyStatus.fillingForm:
        return '尚有7日可填寫';
      case ApplyStatus.reviewingForm:
        return '已於110/07/28送出審查';
      case ApplyStatus.reject:
        return '尚有7日可填寫';
      case ApplyStatus.susscess:
        return '';
      default:
        return '';
    }
  }

}
